<template >

  <div id="app" class="div-in-center">
    <el-form ref="form" :rules="rules" :model="form" label-width="80px" id="form1" >
      <el-row ><el-col span="14" offset="6">
        <el-form-item label="" >
          <div class="size-icon">
          <i class=" el-icon-s-custom"></i>
          </div>
        </el-form-item>
      </el-col>
      </el-row>

      <el-row ><el-col span="18" offset="2" >
      <el-form-item label="账号" prop="user">
        <el-input  placeholder="请输入账号" ref="user1" clearable v-model="form.user"></el-input>
      </el-form-item>
      </el-col>
      </el-row>
      <el-row> <el-col span="18" offset="2">
      <el-form-item label="密码" prop="password">
        <el-input placeholder="请输入密码"  v-model="form.password" show-password></el-input>
      </el-form-item></el-col></el-row>
      <el-row> <el-col span="18" offset="2">
        <el-form-item label="确认"  prop="confirmpass" v-show=confirmshow>
          <el-input placeholder="请再一次输入密码" v-model="confirmpassword"  show-password ></el-input>
        </el-form-item></el-col></el-row>
      <el-row> <el-col span="18" offset="2">
      <el-form-item label="身份 " prop="type">
          <el-radio-group v-model="form.type" >
            <el-radio-button label="学生" name="type"></el-radio-button>
            <el-radio-button label="教师" name="type"></el-radio-button>
            <el-radio-button label="其它工作人员" name="type"></el-radio-button>
          </el-radio-group>
      </el-form-item></el-col></el-row>
      <el-row> <el-col span="18" offset="2">
        <el-form-item label="验证">
          <valid-code v-model="validCode" ref="validCode1" @click="login" l></valid-code>
        </el-form-item></el-col></el-row>
      <el-row> <el-col span="18" offset="4">
        <el-form-item>
          <el-button type="primary" @click="submit('form')" >{{str1}}</el-button>
        <el-button @click="register" >{{str2}}</el-button>
      </el-form-item>
      </el-col></el-row>
    </el-form>
  </div>
</template>
<script>
import  ValidCode from "@/components/verificationCode"
export default {
  name: 'App',
  components: {
    ValidCode

  }, data() {
    var checkpassword = (rule, value, callback) => {
      if(!this.confirmshow)
      {callback()}
      if (this.confirmpassword==='')
      {
        callback(new Error("请再次输入密码！"))
      }
      else if(this.confirmpassword!==this.form.password)
      {
        callback(new Error("两次密码输入不一致！"))
      }else
      {
        callback()
      }

    };
    return {
      validCode:'',
      confirmpassword:'',
      confirmshow:false,
      str1:'立即登录',
      str2:'注册',
      form: {
        user: '',
        password: '',
        type:'',
      },
      rules: {
        user: [
          { required: true, message: '请输入账号', trigger: 'blur' },
          { min: 8, max: 16, message: '长度在 8 到 16 个字符之间', trigger: 'blur' }
        ],password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 8, max: 16, message: '长度在 8到 16 个字符之间', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '请填写身份！', trigger: 'blur' }
        ],confirmpass:[
          {validator:checkpassword,required:true,trigger:'blur'}
        ]

      }
    }
  },methods:{
    submit :function (formname) {
      this.$refs[formname].validate((valid) => {
            if (!this.confirmshow) {
              if (!this.$refs.validCode1.confirmSuccess) {
                alert('未通过验证！')
              } else if (!valid) {
                alert("未通过验证 ")
              } else {
                let formData = new FormData()
                formData.append('user', this.form.user)
                formData.append('password', this.form.password)
                let config = {
                  headers: {
                    'Content-Type': 'multipart/form-data'
                  }
                }
                this.$axios.post('http://localhost:8080/login', formData, config).then(res => {
                      if (res.data.success === "true") {
                        alert("登录成功")
                        window.location.href = "https://www.baidu.com"
                      } else {
                        alert("错误的账号或者密码")
                      }
                    }
                )
              }
            }
            else
            {
              if (!this.$refs.validCode1.confirmSuccess) {
                alert('未通过验证！')
              } else if (!valid) {
                alert("未通过验证 ")
              } else {
                let formData = new FormData()
                formData.append('user', this.form.user)
                formData.append('password', this.form.password)
                let config = {
                  headers: {
                    'Content-Type': 'multipart/form-data'
                  }
                }
                this.$axios.post('http://localhost:8080/register', formData, config).then(res => {
                  if (res.data.success === "true") {
                    alert("注册成功")
                    window.location.href = "https://www.baidu.com"
                  } else {
                    alert("账户已被注册！")
                  }
                    }
                )
              }

            }
          }

        )

    }
    ,getCode(data){
      console.log(data)
      this.validCode = data;//在data中定义一个 validCode:'',用来记录验证码。
    },register()
    {
      if(!this.confirmshow) {
        this.form.password=''
        this.form.user=''
        this.form.type=''
        this.confirmshow = true
        this.str1 = "立即注册"
        this.str2 = '返回登陆'
      }
      else
      {
        this.form.password=''
        this.form.user=''
        this.form.type=''
        this.confirmpassword=''
        this.confirmshow = false
        this.str1 = "立即登录"
        this.str2 = '注册'
      }

    }

  }
}

</script>
