<template>
  <div id="chance" class="div-in-center">
  <el-table
      :data="tableData"
      border
      style="width: 100%">
    <el-table-column
        prop="date"
        label="日期"
        width="100px" >
    </el-table-column>
    <el-table-column
        prop="who"
        label="谁的问题"
        width="100px">
    </el-table-column>
    <el-table-column
        prop="detail"
        label="具体详情"
    width="300px">
    </el-table-column>
  </el-table>
    <el-form ref="form" :model="form" id='form1' label-width="80px">
      <el-form-item label="发生时间">
        <el-col span="8">
          <el-date-picker type="date" placeholder="选择日期"   value-format="yyyy-MM-dd" v-model="form.date" style="width: 100%;"></el-date-picker>
        </el-col>
      </el-form-item >
      <el-form-item label="谁的问题">
        <el-select v-model="form.who" placeholder="请选择">
          <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
              >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="具体详情">
        <el-col span="16">
        <el-input type="textarea" v-model="form.desc"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item >
        <el-col span="10" offset="8" >
        <el-button type="primary" @click="submit()">立即创建</el-button>
          </el-col>
      </el-form-item>
    </el-form>
    </div>
</template>

<script>
export default {
  name:'chance',
  data() {
    return {
      form:{
        date:'',
        who:'',
        desc:'',
      },
      tableData: [],options:[
        {
          value:"周伟",
          label:"周伟"
        },{
        value:'王妤琪',
         label:'王妤琪'
        }
      ]
    }
  },mounted() {

    this.$axios.post('http://106.12.172.208/wyq2').then(res => {

      this.tableData=eval(res.data.ans);

        }
    )
  }
  ,methods:{
    submit: function ()
        {
            let formData = new FormData()
            formData.append('date', this.form.date)
            formData.append('who', this.form.who)
            formData.append('detail',this.form.desc)
            let config = {
              headers: {
                'Content-Type': 'multipart/form-data'
              }
            }
            this.$axios.post('http://106.12.172.208/wyq1', formData, config).then(res => {
              if(res.data.success=='true')
              {
                alert('')
              }
              alert("恭喜你添加成功！")
              location.reload();
                this.mounted()
                }
            )
          }
  }
}
</script>
<style>
#form1 {
  background: white;
}
</style>